<template>
   <div class="WalletSite">
       <Head :data='Title'></Head>
       <div class='site'>
            <div v-for='(item,index) in arr' :key='index' @click='skipSite(item)'>
                <p>
                    <em>
                        <img :src="item.avatar" alt="">
                    </em>
                    <em>{{item.token}}</em>
                </p>
                <span>{{item.address == null ? '绑定地址' : '查看地址'}}</span>
            </div>
       </div>
   </div>
</template>

<script>

import axios from 'axios';
import Head from '../Head';

export default {
    name: 'WalletSite',
    components: { Head },  
    data() {
        return {
            Title:'提币地址',
            arr:[]
        }
    },
    created(){
        this.getSiteName();
    },
    methods:{
        getSiteName(){
            let url = window.globalData.config.domain + '/user/getTokenList';
            axios.post(url,{
                cmd:'123',
                body:{
                    appid:localStorage.appid,
                    uid:localStorage.uid
                }
            }).then( (res) => {
                // console.log(res.data);
                this.arr = res.data;
            })
        },
        skipSite(item){
            if( item.address === '' || item.address === null || item.address === undefined ){
                this.$router.push({path:'/WalletSite/bindSite',query:{num:item.token}})
            }else if( item !== null ){
                let data = JSON.stringify(item)
                this.$router.push({path:'/WalletSite/coin',query:{data:item.token}})
            }

        }
    }

}
</script>

